import city from "./city/city";
import hamburger from "./hamburger/hamburger";
import spoler from "./spoler/spoler";
import parallax from "./parallax/parallax";
import lazyLoading from "./lazy-loading/lazy-loading";
import works from "./js-works/works";
import toTop from "./to-top/to-top"
import linkAnchor from "./link-anchor/link-anchor";
import menu from "./menu-css/menu";

window.addEventListener("DOMContentLoaded", () => {
	if ($('.parallax').length) {
		parallax();
	}
	if ($('.spoler').length) {
		spoler();
	}
	hamburger();
	city();
	works();
	linkAnchor();
	toTop();
	if ($('.menu').length) {
		menu();
	}
	if ($('.lazy-loading').length) {
		lazyLoading();
	}
});